import {SELECT_MENU} from '../actionsType';
const defaultState = {
    index: 0
};
export default (state = defaultState, action)=> {
    switch (action.type) {
        case SELECT_MENU: {
            return Object.assign({}, state, {index: action.payLoad.index});
        }
            break;
        default:
            return state;
    }
}


